@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@400;500;700&display=swap');

.nav_menu{
    padding: 30px 0;

    display: flex;
    justify-content: end;
    gap: 25px;
   
    font-size: 20px;
    font-weight: 500;
    font-family:'Heebo', sans-serif;

}

.nav_menu a{
    color: black;
    text-decoration: none;
}

@media (max-width: 1200px) {
    .nav_menu{
        padding: 0 25px;
    }
}

@media (max-width: 500px) {
    .nav_menu{
        display: none;
    }
}


